*,
*::before,
*::after {
  box-sizing: border-box;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.button {
  background-color: #68b738;
  color: #ffffff;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  text-decoration: none;
  width: 100%;
  flex-wrap: wrap;
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
  border: none;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.page {
  background-color: #ffffff;
  --side-offset: 20px;
}
@media (min-width: 768px) {
  .page {
    --side-offset: 30px;
  }
}
@media (min-width: 1440px) {
  .page {
    --side-offset: calc(50vw - 610px);
  }
}
.page__body {
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
  margin: 0 auto;
  padding: 0;
  color: #000000;
}
.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  background-color: #ffffff;
  margin: 0;
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    position: relative;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    border: none;
    padding: 25px var(--side-offset) 30px;
  }
}
@media (min-width: 1440px) {
  .header {
    padding: 55px var(--side-offset) 30px;
  }
}
@media (min-width: 1440px) {
  .header--desktop {
    padding: 53px var(--side-offset) 20px;
    position: absolute;
    width: 100%;
    top: 0;
    z-index: 1;
    background-color: transparent;
    --current-color: #ffffff;
  }
}
.header__logo-wrapper {
  padding: 10px var(--side-offset) 13px;
}
@media (min-width: 768px) {
  .header__logo-wrapper {
    padding: 0;
  }
}
.header__logo-image:hover {
  opacity: 0.8;
  cursor: pointer;
}
.header__logo-image:active {
  opacity: 0.6;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__logo-image {
    height: auto;
  }
}
.main-nav {
  padding: 0;
}
@media (min-width: 1440px) {
  .main-nav {
    display: flex;
    justify-content: flex-end;
  }
}
.main-nav__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  text-align: center;
  z-index: 100;
  background-color: #ffffff;
}
.main-nav__list--enabled {
  position: absolute;
  display: none;
  width: 100%;
}
.main-nav__list--opened {
  display: flex;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    position: unset;
    flex-direction: row;
    gap: 37px;
    max-width: 443px;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    background-color: transparent;
    max-width: 750px;
  }
  .main-nav__list--desktop {
    max-width: 42vw;
  }
}
.main-nav__toggle {
  position: absolute;
  content: "";
  height: 18px;
  width: 24px;
  background-repeat: no-repeat;
  background-color: #ffffff;
  background-image: url("/img/button-header.svg");
  right: 20px;
  top: 22px;
  cursor: pointer;
  border: none;
}
.main-nav__toggle--opened {
  background-image: url("/img/icon-close.svg");
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__list-item {
  border-top: 1px solid #d9d9d9;
  padding: 21px 20px 23px;
}
@media (min-width: 768px) {
  .main-nav__list-item {
    border-top: none;
    padding: 0;
  }
}
.main-nav__list-item a {
  list-style-type: none;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
}
.main-nav__list-item a:hover {
  opacity: 0.6;
}
.main-nav__list-item a:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__list-item a {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__list-item a--current::before {
    position: absolute;
    content: "";
    background-color: var(--current-color, #68b738);
    height: 2px;
    bottom: -10px;
  }
}
@media (min-width: 1440px) {
  .main-nav__list-item a {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 1440px) {
  .main-nav__list-item--white a {
    color: #ffffff;
  }
}
@media (min-width: 768px) {
  .main-nav__list-item--current {
    position: relative;
  }
  .main-nav__list-item--current::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    background-color: #68b738;
    bottom: -11px;
    left: 0;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .main-nav__list-item--current::after {
    bottom: -7px;
  }
}
@media (min-width: 1440px) {
  .main-nav__list-item--desktop::after {
    background-color: #ffffff;
  }
}
.promo {
  overflow: hidden;
}
.promo__wrapper {
  position: relative;
  text-align: center;
  display: grid;
  justify-items: center;
  justify-content: center;
  padding: 10px var(--side-offset) 11px;
  color: #ffffff;
  background-color: hsl(97, 53%, 47%);
  background-image: linear-gradient(to bottom, hsl(97, 53%, 47%) 302px, #ffffff 302px);
  background-size: 100%;
  z-index: 0;
  border-bottom: none;
}
.promo__wrapper::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 302px;
  inset: 0 0 auto;
  background-image: url("../img/index-background-mobile.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  opacity: 0.2;
  z-index: -1;
}
@media (min-width: 768px) {
  .promo__wrapper::after {
    background-image: none;
  }
}
@media (min-width: 1440px) {
  .promo__wrapper::after {
    width: 50%;
    height: 100%;
    inset: 0 0 auto;
    background-image: url("../img/index-background-desktop.jpg");
    background-position: right center;
    transform: translateX(100%);
  }
}
@media (min-width: 768px) {
  .promo__wrapper {
    display: flex;
    flex-direction: column;
    align-items: baseline;
    background-color: #ffffff;
    background-image: none;
    padding: 32px calc(var(--side-offset) + 40px) 517px;
    position: relative;
    border-top: none;
  }
}
@media (min-width: 1440px) {
  .promo__wrapper {
    display: block;
    margin: 0 auto;
    padding: 223px calc(var(--side-offset) + 80px) 20px;
    background-image: linear-gradient(to right, #ffffff 50%, hsl(97, 53%, 47%) 50%);
    background-position: right bottom;
    background-size: 100%, 50% auto;
    height: 696px;
  }
}
.promo__title {
  margin: 0 auto;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
  font-weight: 400;
  padding-top: 18px;
  padding-bottom: 24px;
  width: 250px;
}
@media (min-width: 768px) {
  .promo__title {
    color: #000000;
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
    padding-left: 46px;
    padding-bottom: 40px;
    padding-top: 0;
    margin: 0;
    order: -3;
    text-align: left;
    width: 95%;
  }
}
@media (min-width: 1440px) {
  .promo__title {
    width: 33vw;
    padding: 0;
    padding-bottom: 40px;
    max-width: 18ch;
  }
}
.promo__slogan {
  padding-bottom: 31px;
  font-size: 14px;
  line-height: 14px;
  color: #ffffff;
  margin: 0 auto;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .promo__slogan {
    text-align: left;
    padding-left: 49px;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: #000000;
    padding-bottom: 44px;
    margin: 0;
    order: -2;
  }
}
@media (min-width: 1440px) {
  .promo__slogan {
    padding: 0;
    width: 39vw;
  }
}
.promo__button {
  font-size: 14px;
  line-height: 18px;
  padding: 7px 44px;
  width: 280px;
  min-height: 40px;
  align-items: center;
  margin-top: 3px;
}
@media (min-width: 768px) {
  .promo__button {
    margin-left: 50px;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    order: -1;
    padding: 13px 26px;
    width: 244px;
    min-height: 54px;
  }
}
@media (min-width: 1440px) {
  .promo__button {
    margin: 0;
    margin-top: 50px;
  }
}
@media (min-width: 768px) {
  .promo__image {
    position: absolute;
    height: 609px;
    width: auto;
    bottom: -11px;
    right: 29px;
    z-index: -1;
  }
}
@media (min-width: 1440px) {
  .promo__image {
    height: auto;
    width: 552px;
    z-index: 1;
    right: initial;
    left: calc(50% - 162px);
    top: 140px;
  }
}
.box {
  padding: 0 var(--side-offset) 40px;
}
@media (min-width: 768px) {
  .box {
    padding: 0 var(--side-offset) 0;
  }
}
.box__list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  margin: 0 auto;
  padding: 0;
  gap: 16px;
}
@media (min-width: 768px) {
  .box__list {
    flex-direction: row;
    counter-reset: box;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0;
    gap: 37px 50px;
  }
}
@media (min-width: 1440px) {
  .box__list {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-template-rows: 1fr;
    column-gap: 70px;
  }
}
.box__item {
  width: 100%;
  min-height: 65px;
}
@media (min-width: 768px) {
  .box__item {
    min-height: 233px;
    z-index: 1;
    counter-increment: box;
    flex-basis: calc(50% - 30px);
    padding-top: calc(80px + 31px);
    margin-bottom: 49px;
  }
}
.box__item-nutrients {
  position: relative;
}
.box__item-nutrients::before {
  position: absolute;
  content: "";
  background-image: url("/img/nutrients.svg");
  width: 60px;
  height: 60px;
  bottom: -3px;
  top: -2px;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .box__item-nutrients::before {
    background-image: url("/img/nutrients-desktop.svg");
    width: 80px;
    height: 80px;
    top: 0;
  }
}
.box__item-composition {
  position: relative;
}
.box__item-composition::before {
  position: absolute;
  content: "";
  background-image: url("/img/composition.svg");
  width: 60px;
  height: 60px;
  bottom: -3px;
  top: -2px;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .box__item-composition::before {
    background-image: url("/img/composition-desktop.svg");
    width: 80px;
    height: 80px;
    top: 0;
  }
}
.box__item-meal {
  position: relative;
}
@media (min-width: 768px) {
  .box__item-meal {
    padding-top: 110px;
  }
}
.box__item-meal::before {
  position: absolute;
  content: "";
  background-image: url("/img/meal.svg");
  width: 60px;
  height: 60px;
  bottom: -3px;
  top: -2px;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .box__item-meal::before {
    background-image: url("/img/date-desktop.svg");
    width: 80px;
    height: 80px;
    top: -2px;
  }
}
@media (min-width: 1440px) {
  .box__item-meal::before {
    left: 5px;
  }
}
.box__item-date {
  position: relative;
}
@media (min-width: 768px) {
  .box__item-date {
    padding-top: 110px;
  }
}
.box__item-date::before {
  position: absolute;
  content: "";
  background-image: url("/img/date.svg");
  width: 60px;
  height: 60px;
  bottom: -3px;
  top: -4px;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .box__item-date::before {
    background-image: url("/img/meal-desktop.svg");
    width: 80px;
    height: 80px;
    top: -2px;
  }
}
@media (min-width: 1440px) {
  .box__item-date::before {
    left: 7px;
  }
}
.box__title {
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  margin: 0 auto;
  padding-bottom: 40px;
  padding-top: 3px;
}
@media (min-width: 768px) {
  .box__title {
    padding-top: 25px;
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
    padding-bottom: 73px;
  }
}
@media (min-width: 1440px) {
  .box__title {
    padding-top: 57px;
  }
}
.box__item-description {
  font-family: "Arial", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  text-align: left;
  margin: 0 0 0 80px;
}
@media (min-width: 768px) {
  .box__item-description {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    margin: 0;
    width: 78%;
    position: relative;
    text-align: left;
  }
  .box__item-description::after {
    content: counter(box);
    font-family: "Oswald", "Arial", "Helvetica", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;
    position: absolute;
    top: -151px;
    right: 40px;
    z-index: -10;
  }
}
@media (min-width: 1440px) {
  .box__item-description {
    min-width: 250px;
  }
  .box__item-description::after {
    top: -152px;
    right: 10px;
  }
}
@media (min-width: 768px) {
  li:nth-child(1) p::after {
    right: 28px;
  }
}
@media (min-width: 768px) {
  li:nth-child(2) p::after {
    right: 8px;
  }
}
@media (min-width: 768px) {
  li:nth-child(3) p::after {
    right: 4px;
  }
}
@media (min-width: 768px) {
  li:nth-child(4) p::after {
    right: 4px;
  }
}
@media (min-width: 1440px) {
  li:nth-child(3) p::after,
  li:nth-child(4) p::after {
    right: -4px;
  }
}
.catalog-products {
  display: grid;
  gap: 20px;
  padding: 10px var(--side-offset) 20px;
  border-top: none;
}
@media (min-width: 768px) {
  .catalog-products {
    gap: 30px;
  }
}
@media (min-width: 1440px) {
  .catalog-products {
    padding-top: 82px;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }
}
.programs {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 20px;
  min-width: 280px;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .programs {
    padding-top: 26px;
  }
}
@media (min-width: 1440px) {
  .programs {
    display: grid;
    padding: 0;
  }
}
