  content: "";
  background-repeat: no-repeat;
  background-size: contain;
}
.programs--skinny::before {
  width: 50px;
  height: 53px;
  background-image: url("/img/cat-skinny.svg");
  top: 20px;
  left: 22px;
}
@media (min-width: 768px) {
  .programs--skinny::before {
