  }
}
.programs--fat::before {
  width: 67px;
  height: 50px;
  background-image: url("/img/fat-cat.svg");
  top: 26px;
  left: 13px;
}
@media (min-width: 768px) {
  .programs--fat::before {
    width: 268px;
    height: 200px;
    right: 27px;
    left: initial;
    top: 93px;
  }
}
@media (min-width: 1440px) {
  .programs--fat::before {
    width: 134px;
    height: 100px;
    left: 33px;
    top: 46px;
  }
}
.programs__subtitle {
  text-transform: uppercase;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  margin: 9px 0 7px 71px;
}
@media (min-width: 768px) {
  .programs__subtitle {
    padding-left: 32px;
    padding-top: 52px;
    font-weight: 400;
    font-size: 36px;
    line-height: 36px;
    margin: 0;
    max-width: 400px;
  }
}
@media (min-width: 1440px) {
  .programs__subtitle {
    padding-top: 77px;
    padding-left: 22px;
    justify-self: center;
    max-width: 24vw;
  }
}
@media (min-width: 1440px) {
  .programs__subtitle--weight {
    margin-top: 0;
    padding-left: 62px;
  }
}
.programs__description {
  position: relative;
  font-family: "Arial", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  padding-top: 22px;
  padding-bottom: 23px;
  border-bottom: 1px solid #d9d9d9;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .programs__description {
    padding: 24px 0 28px 32px;
    border: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    width: 56%;
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .programs__description {
    padding: 73px 51px 0;
    width: 100%;
  }
}
.arrow-link {
  position: relative;
  text-align: left;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 16px;
  color: #000000;
  margin: 0;
  padding-top: 18.5px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (min-width: 768px) {
  .arrow-link {
    padding: 24px 0 35px 32px;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 1440px) {
  .arrow-link {
    padding: 24px 0 55px 52px;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    max-width: 36vw;
  }
}
.arrow-link:active {
  color: rgba(0, 0, 0, 0.3);
}
.arrow-link:hover::before,
.arrow-link:focus::before {
  transform: scaleX(1.3);
}
.arrow-link:hover::after,
.arrow-link:focus::after {
  transform: translateX(72%);
}
.arrow-link:active::before {
  transform: scaleX(1.29);
  color: rgba(0, 0, 0, 0.3);
}
.arrow-link:active::after {
  transform: translateX(72%);
  color: rgba(0, 0, 0, 0.3);
}
.arrow-link::before,
.arrow-link::after {
  content: "";
  background-color: currentColor;
  order: 1;
}
.arrow-link::before {
  width: 18px;
  height: 2px;
  color: #000000;
  margin-left: 15px;
  transform-origin: left center;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .arrow-link::before {
    margin-left: 25px;
  }
}
.arrow-link::after {
  width: 7px;
  height: 10px;
  clip-path: polygon(0 0, 100% 50%, 0 100%);
}
.example {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: #eaeaea;
  padding: 25px var(--side-offset) 11px;
}
@media (min-width: 768px) {
  .example {
    border-top: 1px solid #e7e7e7;
    background-image: linear-gradient(to bottom, #ffffff 0, #ffffff 57.5%, #eaeaea 0%);
    padding: 25px var(--side-offset) 100px;
  }
}
@media (min-width: 1440px) {
  .example {
    background-color: #f2f2f2;
