.list-item {
  list-style-type: none;
  display: flex;
}

.list-item__text {
  font-family: @accent-font;
  font-style: normal;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: @dark-colors;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.list-item__nickname {
  display: grid;
  grid-template-columns: auto minmax(220px, 1fr);
  align-items: center;
  gap: 13px;
  width: 100%;

  @media (min-width: @tablet-width) {
    column-gap: 17px;
  }

  @media (min-width: @desktop-width) {
    column-gap: 15px;
  }
}

.list-item__weight {
  display: grid;
  grid-template-columns: auto minmax(191px, 1fr);
  column-gap: 16px;
  align-items: center;
  width: 100%;

  @media (min-width: @tablet-width) {
    column-gap: 21px;
  }

  @media (min-width: @desktop-width) {
    column-gap: 14px;
  }
}

.list-item__age {
  display: grid;
  grid-template-columns: auto  minmax(167px, 1fr);
  column-gap: 15px;
  align-items: center;
  width: 100%;

  @media (min-width: @tablet-width) {
    column-gap: 18px;
  }
}

.list-item__input {
  border: 1px solid #e7e7e7;
  padding-top: 13px;
  padding-bottom: 11px;
  padding-left: 15px;
  font-family: @accent-font;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: @dark-colors;
  cursor: pointer;

  &:focus {
    outline: 2px solid @dark-colors;
  }


  &:hover {
    border: 1px solid @states-colors;
  }

  &:invalid {
    outline: 2px solid #ff8282;
  }

  @media (min-width: @tablet-width) {
    padding-left: 18px;
    padding-right: 10px;
    padding-top: 9px;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 21px;
  }
}

@media (min-width: @desktop-width) {
  li:nth-child(3) .list-item__input {
    padding-left: 17px;
  }
}
